<template>
  <div class="area">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="iframeBox">
          <iframe src="#/areaExample1" style="min-height:500px;"></iframe>
          <iframe src="#/areaExample2" style="min-height:660px;"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">area 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import area from "cy_static/area"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;script type="text/javascript"&gt;
    import area from "cy_static/area"; 
    var insarea = area({
      maxNum : 3,
      items : [ {
        "id" : "aa",
        "value" : "0"
      }, {
        "id" : "bb",
        "value" : "0"
      }, {
        "id" : "cc",
        "value" : "0"
      } ],
      isArea : false,// 级别关联
      data : [ 
            [{text : "年",value : "年"}], 
            [{text : "月",value : "月"}], 
            [{text : "日",value : "日"}]
      ]
    });
    insarea.show();
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">构造方法</h2>				
            <p><a>area</a>(params : Object) : <a>Object</a></p>
						<p>支持级联滑动（如地区）与非级联滑动(日期选择)等；</p>
            <table class="table-props border">
              <tr>
                <th>参数名</th>
                <th>类型</th>
                <th>描述</th>
                <th>默认值</th>
                <th class="text-c">必需</th>
              </tr>
              <tr>
                <td>elem</td>
                <td class="atn">String||Object</td>
                <td>绑定点击显示元素</td>
                <td>""</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>isArea</td>
                <td class="atn">Boolean</td>
                <td>是否级联查询</td>
                <td>true</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>data</td>
                <td class="atn">Array</td>
                <td>非常级联时的数据</td>
                <td>[]</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>emptyNum</td>
                <td class="atn">Number</td>
                <td>选择item前面空几个选项</td>
                <td>2</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>maxNum</td>
                <td class="atn">Number</td>
                <td>最大显示级联数量</td>
                <td>100</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>text</td>
                <td class="atn">String</td>
                <td>数量文本属性</td>
                <td>text</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>value</td>
                <td class="atn">String</td>
                <td>数量值属性</td>
                <td>value</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>items</td>
                <td class="atn">Array</td>
                <td>各级联项属性</td>
                <td>[]</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>ajaxFun</td>
                <td class="atn">Function</td>
                <td>级联时查询数据调用的方法</td>
                <td>null</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onShow</td>
                <td class="atn">Function</td>
                <td>插件显示时的回调</td>
                <td>null</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onCancel</td>
                <td class="atn">Function</td>
                <td>点击取消时的回调</td>
                <td>null</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onSubmit</td>
                <td class="atn">Function</td>
                <td>点击确认时的回调</td>
                <td>null</td>
                <td class="text-c">N</td>
              </tr>
            </table>
      </div>

      <div class="apiModule">
        <h2 class="cy_h2">实例方法</h2>
        <ul class="ul-props">
					<li>
						<p><a>show</a>() : <a>Void</a></p>
						<p>显示插件；</p>
					</li>
					<li>
						<p><a>hide</a>() : <a>Void</a></p>
						<p>隐藏插件；</p>
					</li>		
					<li>
						<p><a>setActivated</a>(isActivated) : <a>Void</a></p>
						<p>是否激活；</p>
					</li>
					<li>
						<p><a>scrollTop</a>(index : Number) : <a>Void</a></p>
						<p>把滚动条滑动到固定位置；</p>
					</li>
					<li>
						<p><a>onShow</a>(func : Function) : <a>Void</a></p>
						<p>把滚动条滑动到固定位置；</p>
					</li>
					<li>
						<p><a>onCancel</a>(func : Function) : <a>Void</a></p>
						<p>把滚动条滑动到固定位置；</p>
					</li>
					<li>
						<p><a>onSubmit</a>(func : Function) : <a>Void</a></p>
						<p>把滚动条滑动到固定位置；</p>
					</li>
					<li>
						<p><a>ajaxFun</a>(func : Function) : <a>Void</a></p>
						<p>把滚动条滑动到固定位置；</p>
					</li>
					<li>
						<p><a>destroy</a>() : <a>Void</a></p>
						<p>销毁实例里所有事件；</p>
					</li>
				</ul>        
      </div>




    </div> 
  </div>
</template>

<script>
import {trim} from "../../../assets/js/codeStyle";
export default {
  name: 'area',
  data () {
    return {
      msg: 'area',
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
        var m = this;
        ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
            var dom, valDom, index = 0, getDom = function() {
                index++;
                dom = m.$refs[element + index];
                valDom = m.$refs[element + "Val" + index];
            };
            getDom();
            while (dom) {
                dom.codemirror.setValue(trim(valDom.innerHTML));
                getDom();
            }
        });
  }
}
</script>  
<style scoped>
  .cy_example {overflow-y: scroll;right: -15px;}  
  .cy_example .iframeBox{position:absolute;top:43px;width:100%;bottom:0}
  .cy_example .iframeBox iframe{height: 50%;}
</style>



